<template>
  <div class="login-box">
    <div class="form-box">
      <div class="top">Login</div>
      <el-form
        :rules="rules"
        ref="loginForm"
        class="form-style"
        :label-position="labelPosition"
        label-width="80px"
        :model="formLabelAlign"
      >
        <el-form-item prop="name" label="用户名">
          <el-input autocomplete="off" v-model="formLabelAlign.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pwd">
          <el-input autocomplete="off" type="password" v-model="formLabelAlign.pwd"></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="submitForm">登录</el-button>
    </div>
  </div>
</template>
<script>
import { bus } from "@/public/eventBus.js";
export default {
  name: "Login",
  data() {
    return {
      labelPosition: "left",
      formLabelAlign: {
        name: "",
        pwd: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 15, message: "长度在 3 到 15 个字符", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 15, message: "长度在 3 到 15 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm() {
      this.$refs.loginForm.validate(valid => {
        if (
          valid &&
          this.formLabelAlign.pwd !== "123456"
        ){
            this.$message.error('密码错误')
        }
        else if (
          valid &&
          this.formLabelAlign.pwd == "123456"
        ) {
        localStorage.setItem('auth', 'auth');
        localStorage.setItem('username', this.formLabelAlign.name);
        bus.$emit('changeUsername', this.formLabelAlign.name)
          this.$router.push({ path: "/home" });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.login-box {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  .form-box {
    width: 450px;
    text-align: center;
    border: 1px solid #409eff;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    padding-bottom: 20px;
    .form-style {
      margin-left: 20px;
      margin-right: 20px;
      margin-top: 20px;
    }
    .top {
      height: 40px;
      background: #409eff;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      font-size: 18px;
    }
  }
}
</style>