<template>
  <div>
    <el-table :border="border" :data="tableData">
      <el-table-column v-if="hasSelect" align="center" type="selection" width="55"></el-table-column>
      <el-table-column
        :align="item.align"
        :prop="item.prop"
        :label="item.label"
        :width="item.width"
        v-for="(item) in cols"
        :key="item.prop"
      >
        <template v-slot="scope">
          <slot :name="item.prop" :row="scope.row">{{ scope.row[item.prop] }}</slot>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "CommonTable",
  props: {
    hasSelect: {
      type: Boolean,
      default: false
    },
    border: {
      type: [Boolean],
      default: true
    },
    tableData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    cols: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  methods: {
    getScope(index) {
      const data = this.tableData;
      // console.log(data[index], prop)
      console.log(data[index]);
      // return 'abc'
      return data[index];
    }
  }
};
</script>
