<template>
  <el-dialog title="设置范围" :visible="isShow" center :append-to-body="true" width="30%" @close="close">
    <div>
      <CheckGroup groupName="one" v-model="group"></CheckGroup>
      <CheckChild
        groupName="one"
        v-model="item.isCheck"
        :text="item.name"
        :key="item.id"
        v-for="item in list"
      />
    </div>
    <div slot="footer">
      <el-button @click="close">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>
<script>
import CheckGroup from "./checkbox/CheckGroup.vue";
import CheckChild from "./checkbox/CheckChild.vue";
export default {
  name: "RangeDia",
  components: {
    CheckChild,
    CheckGroup
  },
  props: {
    isShow: {
        type: Boolean,
        default: false
    }
  },
  data() {
    return {
      group: "",
      list: [
        {
          name: "范围1",
          isCheck: true
        },
        {
          name: "范围2",
          isCheck: true
        },
        {
          name: "范围3",
          isCheck: true
        },
        {
          name: "范围4",
          isCheck: true
        },
        {
          name: "范围5",
          isCheck: true
        }
      ],
    
    };
  },
  methods: {
    close() {
       this.$emit('update:isShow', false)
    },
    confirm() {
        this.$emit('update:isShow', false)
    }
  }
};
</script>
