<template>
  <div class="top-bar-box">
    <div class="user-img">
      <span class="el-icon-user-solid"></span>
      <el-dropdown>
        <span class="el-dropdown-link">
          {{username}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="exit">退出</el-dropdown-item>
          <el-dropdown-item @click.native="openPersonDialog">个人中心</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="isShow"
      center
      :append-to-body="true"
      width="30%"
      @close="close"
      :close-on-click-modal="false"
      custom-class="person-dia"
    >
      <ul>
        <li class="row">
          <span class="label">修改密码:</span>
          <el-input></el-input>
          <el-button type="primary">修改</el-button>
        </li>
        <li class="row">
          <span class="label">修改邮箱:</span>
          <el-input></el-input>
          <el-button type="primary">修改</el-button>
        </li>
      </ul>
      <!-- <div slot="footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </div>-->
    </el-dialog>
  </div>
</template>
<script>
import { bus } from "@/public/eventBus.js";
export default {
  name: "TopBar",
  data() {
    return {
      isShow: false,
      username: ""
    };
  },
  mounted() {
    this.username = localStorage.getItem("username");
    bus.$on("changeUsername", name => {
      this.username = name;
    });
  },
  methods: {
      exit() {
          localStorage.clear();
          this.$router.push({path: '/login'})
      },
    openPersonDialog() {
      this.isShow = true;
    },
    close() {
      this.isShow = false;
    },
    confirm() {
      this.isShow = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.top-bar-box {
  height: 50px;
  background: #ecf5ff;
  position: relative;

  .user-img {
    font-size: 40px;
    position: absolute;
    right: 20px;
    .user-name {
      font-size: 14px;
    }
  }
}
</style>
<style lang="scss" >
.person-dia {
  .label {
    width: 90px;
  }
  .row {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
}
</style>