import VueRouter from 'vue-router'
import Vue from 'vue';

import OrderList from '@/pages/OrderList';
import Home from '@/pages/Home'
import Login from '@/pages/Login'
import UserList from '@/pages/UserList'

Vue.use(VueRouter)

const router = new VueRouter({
    routes: [{
        name: 'User',
        path: '/user',
        component: UserList,
        meta: {
            auth: true
        }
    },
    {
        name: 'Index',
        path: '/',
        component: Home,
        meta: {
            auth: true
        } 
    },
    {
        name: 'Home',
        path: '/home',
        component: Home,
        meta: {
            auth: true
        }
    },{
        name: 'Order',
        path: '/order',
        component: OrderList,
        meta: {
            auth: true
        }
    },{
        name: 'Login',
        path: '/login',
        component: Login
    },{
        name: 'NotFound',
        path: '*',
        component: Login
    }]
})
router.beforeEach((to,from,next) => {
    console.log(to, from, next);
    const shouldAuth = to.meta.auth;
    const hasAuth = localStorage.getItem('auth');
    if((shouldAuth && hasAuth) || !shouldAuth) {
        next();
    }
    else if(shouldAuth && !hasAuth){
        next({
            path: '/login'
        })
    }
})

console.log(router)
export default router
