<template>
  <div id="app">
        <LeftMenu v-show="isShowMenu" />
        <div class="right-box">
          <top-bar v-show="isShowMenu"></top-bar>
          <transition name="fade-transform" mode="out-in">
          <router-view></router-view>
        </transition>
        </div>
  </div>
</template>

<script>
import LeftMenu from "@/components/LeftMenu";
import TopBar from "@/components/TopBar";
export default {
  name: "App",
  components: { LeftMenu, TopBar },
  computed: {
    isShowMenu() {
      const name = this.$route.name;
      // const authArr = ['Home', 'Index', '']
      if (name == "Login" || name == 'NotFound') {
        return false;
      }
      return true;
    }
  }
};
</script>

