<template>
  <div class="order-list-box">
    <ul class="nav">
      <li class="row">
        <span class="label">订单号:</span>
        <el-input v-model="navInfo.orderId" class="input"></el-input>
        <span class="label">用户名:</span>
        <el-input v-model="navInfo.username" class="input"></el-input>
        <span class="label">订单状态:</span>
        <el-select class="select" v-model="navInfo.status" placeholder="请选择">
          <el-option
            v-for="item in statusOption"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-button class="search" type="primary">查询</el-button>
      </li>
    </ul>
    <common-table :tableData="TableInfo.data" :cols="TableInfo.cols">
        <template #status="scope">
            <el-tag :type="getStatusType(scope)">{{getStatus(scope)}}</el-tag>
        </template>
    </common-table>
  </div>
</template>
<script>
   import orderListData from '../Json/orderList.json'
export default {
  name: "OrderList",
  data() {
    return {
      TableInfo: {
        data: [
          {
            orderId: "slsdksldsldsd",
            date: "2017-03-25"
          }
        ],
        cols: [
          { prop: "username", label: "用户名", align: "center" },

          {
            prop: "orderId",
            label: "订单编号",
            align: "center"
          },
          {
            prop: "date",
            label: "日期",
            align: "center"
          },
          {
            prop: "price",
            label: "价格",
            align: "center"
          },
          {
            prop: "status",
            label: "状态",
            align: "center"
          }
        ]
      },
      navInfo: {
        orderId: "",
        username: "",
        status: ""
      },
      statusOption: [
        {
          label: "已完成",
          value: "complete"
        },
        {
          label: "已取消",
          value: "cancel"
        },
        {
          label: "已付款",
          value: "pay"
        },
        {
          label: "设计中",
          value: "designing"
        },
        {
          label: "待设计",
          value: "forDesign"
        }
      ]
    };
  },
  mounted() {
      console.log(orderListData)
    this.TableInfo.data = orderListData.data;
  },
  methods: {
      getStatusType(scope) {
        const status = scope.row.status;
        const obj = {
            cancel: 'danger',
            forDesign: '',
            designing: 'success',
            pay: 'warning',
            complete: 'info'
        };
        return obj[status] || '';
      },
    getStatus(scope) {
        console.log(scope.row, 'ssssssssss')
        const status = scope.row.status;
        const obj = {
            cancel: '已取消',
            forDesign: '待设计',
            designing: '设计中',
            pay: '已支付',
            complete: '已完成'
        };
        return obj[status] || '';
    }
  }
};
</script>
<style lang="scss" scoped>
.order-list-box {
  padding: 20px;
  .nav {
    // margin-bottom: 20px;
    .search{
        margin-bottom: 20px;
        margin-left: 20px;
    }
    .label, .input, .select{
        padding-bottom: 20px;
    }
    .label {
      width: 70px;
    }
    .input {
      width: 250px;
      margin-right: 10px;
    }
  }
}
.row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
</style>