<template>
  <div class="home-box">
    <ul class="top">
      <li class="row">
        <div class="add">新增数据</div>
        <span>新增用户52</span>
      </li>
      <li class="row mt-10">
        <div class="all">全部数据</div>
        <span>全部用户2352</span>
      </li>
    </ul>
    <div class="charts" ref="homeChart"></div>
  </div>
</template>
<script>
import * as Echarts from "echarts";
export default {
  name: "Home",
  mounted() {
    console.log(Echarts);
    const dom = this.$refs.homeChart;
    if(!dom) {
        return false;
    }
    const option = {
        tooltip: {
        trigger: 'axis',
        axisPointer: {            
            type: 'shadow'        
        }
    },
    xAxis: {
        type: 'category',
        data: ['一月', '二月', '三月', '四月', '五月', '六月', '七月']
    },
    yAxis: {
        type: 'value'
    },
    series: [{
        data: [120, 300, 450, 180, 270, 150, 130],
        color: '#0170fe',
        barMaxWidth: '24px',
        type: 'bar'
    }]
};
    const instance = Echarts.init(dom);
    instance.setOption(option)
  }
};
</script>
<style lang="scss" scoped>
.home-box {
    padding: 20px;
    .mt-10{
        margin-top: 10px;
    }
  .top {
    padding: 5px;
    .add,.all{
        padding: 10px;
        border-radius: 4px;
        margin-right: 10px;
    }
    .add{
      
        background: #e1f3d8;
        color: #67c23a;
    }
    .all{
       
        background: #ecf5ff;
