<template>
  <div class="left-menu-box">
    <ul>
      <li class="menu-item" @click="handleRoute(item.path)" v-for="item in menuData" :key="item.id">
        <div  :class="routeIsActive(item.routeName)">
            <span :class="item.icon"></span>
            {{item.name}}</div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "LeftMenu",
  data() {
    return {
      menuData: [
        {
          name: "首页",
          path: "/home",
          routeName: 'Home',
          icon: 'el-icon-house'
        },
        {
          name: "订单列表",
          path: "/order",
          routeName: 'Order',
          icon: 'el-icon-s-order'
        },
        {
          name: "人员列表",
          path: "/user",
          routeName: 'User',
          icon: 'el-icon-user'
        }
      ]
    };
  },
  
  mounted() {
      console.log(this.$route)
  },
  methods: {
    routeIsActive(itemName) {
        const name = this.$route.name;
        if(name === itemName) {
            return 'active'
        }
        return ''
    },
    handleRoute(path) {
        this.$router.push({
            path: path
        })
    }
  }
};
</script>
<style lang="scss" scoped>
.left-menu-box {
  min-width: 200px;
  padding-top: 50px;
  width: 200px;
  background: #ecf5ff;
  height: 100vh;
  .menu-item{
      padding-left: 60px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      color: #bbb;
      height: 40px;
      border-bottom: 1px solid #409eff;
        border-left: 1px solid #409eff;
        border-right: 1px solid #409eff;
      cursor: pointer;
      font-size: 16px;
      &:hover{
        color: #409eff
      }
      &:first-child{
        border-top: 1px solid #409eff;
      }
  }
  .active{
      color: #0170fe;
    //   font-weight: bold;
  }
}
</style>